.wrapper {
    position: absolute;
    display: none;
    z-index: 3;
    width: 100%;
    height: 100%;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--color-bg-grey-popup);
}

.wrapper__popup {
    position: fixed;
    margin: auto;
    width: 300px;
    height: 557px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--color-bg-white);

    @include tablet {
      width: 600px;
    }

    @include desktop {
      width: 900px;
      height: 703px;
    }
}

.wrapper__popup-close-button {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 25px;
    height: 25px;
    border: none;
    display: block;
    background-color: transparent;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 10px;
      right: 0px;
      width: 24px;
      height: 2px;
      background-color: var(--color-main-black);
    }
  
    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }
  
    &::after {
      transform: rotate(-45deg);
    }
}

.wrapper__popup-title {
  font-family: var(--font-family-main);
  font-size: 30px;
  margin-left: 15px;

  @include desktop {
    font-size: 40px;
    margin-left: 15px;
    margin-bottom: 13px;
    margin-top: 24px;
  }
}


.wrapper__popup-block {
  overflow: hidden;
  width: 100%;
  height: 40%;
  position: relative;

  @include tablet {
    height: 55%;
  }

  @include desktop {
    height: 69%;
  }
}

.wrapper__popup-img {
  position: absolute;
  width: 100%;
  height: auto;
}

.wrapper__popup-describe {
  margin-top: 20px;
  padding-right: 10px;
  padding-left: 10px ;
  font-family: var(--font-family-little);
  font-size: 14px;
}
