:root {
  --color-bg-white: #ffffff;
  --color-bg-grey: #F6F6F6;
  --color-bg-grey-popup: rgba(0,0,0, 0.8);
  --color-dark-grey:  #828282;
  --color-main-black: #000000;
  --font-family-main: 'Fjalla One', 'Abril Fatface' ,sans-serif;
  --font-family-little: Roboto, 'Fjalla One'; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  min-width: 300px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  box-sizing: border-box; }
  @media (min-width: 768px) {
    .container {
      min-width: 748px; } }
  @media (min-width: 1200px) {
    .container {
      max-width: 1024px; } }

.page {
  position: relative; }

.header {
  background-color: var(--color-bg-white);
  font-family: var(--font-family-main); }

.header__title {
  font-size: 47px;
  line-height: 58px;
  width: 124px;
  height: 106px; }
  @media (min-width: 768px) {
    .header__title {
      margin: 0; } }

.header__photo-demonstration {
  margin: 0 auto;
  margin-bottom: 50px;
  display: block;
  width: 300px;
  height: 478px;
  background-image: url("../img/me-mobile.jpg");
  background-size: auto;
  background-repeat: no-repeat; }
  @media (min-width: 768px) {
    .header__photo-demonstration {
      margin-top: 70px;
      margin-bottom: 0;
      background-image: url("../img/me-desktop.jpg");
      width: 100%;
      max-width: 945px;
      height: 387px; } }
  @media (min-width: 1024px) {
    .header__photo-demonstration {
      margin-top: 100px;
      margin-bottom: 100px; } }

.header__about-me {
  margin: 0;
  margin-bottom: 36px;
  font-family: var(--font-family-little);
  width: 205px;
  height: 44px; }
  @media (min-width: 768px) {
    .header__about-me {
      margin: 0;
      align-self: flex-end; } }

nav {
  position: relative; }

.nav__button {
  position: absolute;
  top: -3px;
  right: 0px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none; }
  @media (min-width: 768px) {
    .nav__button {
      display: none; } }

.nav__closed .nav__button::before {
  content: "";
  position: absolute;
  top: 15px;
  right: 14px;
  width: 24px;
  height: 2px;
  background-color: var(--color-main-black);
  box-shadow: 0 8px 0 0 var(--color-main-black), 0 16px 0 0 var(--color-main-black); }

.nav__opened .nav__button {
  top: -3px;
  right: -0px; }
  .nav__opened .nav__button::before, .nav__opened .nav__button::after {
    content: "";
    position: absolute;
    top: 26px;
    right: 13px;
    width: 24px;
    height: 2px;
    background-color: var(--color-main-black); }
  .nav__opened .nav__button::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .nav__opened .nav__button::after {
    transform: rotate(-45deg); }

.nav__list {
  list-style: none;
  font-size: 25px;
  line-height: 42px;
  color: var(--color-dark-grey); }
  @media (min-width: 768px) {
    .nav__list {
      padding-bottom: 15px;
      border-bottom: 1px solid; } }

.nav__list a {
  color: var(--color-bg-black);
  text-decoration: none; }

.nav__opened .nav__list {
  display: block; }

.nav__closed .nav__list {
  display: none; }
  @media (min-width: 768px) {
    .nav__closed .nav__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      padding: 0; } }

.nav__list-element {
  cursor: pointer; }
  .nav__list-element:link, .nav__list-element:visited, .nav__list-element:hover, .nav__list-element:active {
    color: var(--color-main-black); }

.nav__language {
  text-decoration: none;
  color: var(--color-main-black);
  height: 10px; }
  @media (min-width: 768px) {
    .nav__language {
      align-self: flex-end;
      transform: rotate(90deg);
      margin-bottom: 25px; } }

@media (min-width: 768px) {
  .nav__block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; } }

.about-me {
  padding-top: 50px;
  padding-bottom: 29px;
  background-color: var(--color-bg-grey);
  color: var(--color-main-black);
  font-family: var(--font-family-little);
  font-size: 18px;
  line-height: 22px;
  text-align: center; }

.about-me .about-me__title {
  margin-bottom: 29px;
  font-size: 34px;
  line-height: 42px;
  text-align: center; }

.about-me__describe-first,
.about-me__describe-second {
  padding-bottom: 29px; }

.skills {
  padding-top: 50px;
  background-color: var(--color-bg-white);
  text-align: center; }

.skills__title {
  color: var(--color-main-black);
  font-family: var(--font-family-main);
  font-size: 34px;
  line-height: 42px;
  text-align: center;
  margin-bottom: 50px; }

.skills__advantages {
  margin-bottom: 40px;
  font-size: 18px;
  line-height: 22px;
  font-family: var(--font-family-little); }

.skills__list {
  position: relative;
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }

.skills__element {
  margin-left: 20px;
  margin-right: 20px;
  cursor: pointer; }
  .skills__element:hover > .skills__list-block {
    display: block; }
  @media (min-width: 768px) {
    .skills__element {
      margin-left: 35px;
      margin-right: 35px; } }
  @media (min-width: 1024px) {
    .skills__element {
      margin-left: 45px;
      margin-right: 45px; } }
  @media (min-width: 1200px) {
    .skills__element {
      margin-left: 61px;
      margin-right: 61px; } }

.skills__img {
  width: 80px;
  height: 80px;
  border-radius: 20px; }

.skills__list-block {
  display: none;
  padding-top: 25px;
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 200px;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  background-color: var(--color-bg-grey);
  border: 1px solid;
  border-radius: 15px; }
  @media (min-width: 768px) {
    .skills__list-block {
      min-width: 350px; } }
  @media (min-width: 1024px) {
    .skills__list-block {
      min-width: 450px; } }

.skills__button-close {
  position: absolute;
  top: 7px;
  right: 16px;
  display: block;
  width: 25px;
  height: 25px;
  border: none;
  background-color: transparent; }
  .skills__button-close::before, .skills__button-close::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 0px;
    width: 24px;
    height: 2px;
    background-color: var(--color-main-black); }
  .skills__button-close::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .skills__button-close::after {
    transform: rotate(-45deg); }
  @media (min-width: 1024px) {
    .skills__button-close {
      display: none; } }

.portfolio {
  margin-top: 50px;
  padding-top: 50px;
  padding-bottom: 1px;
  text-align: center;
  background-color: var(--color-bg-grey); }

.portfolio__title {
  font-size: 34px;
  line-height: 42px;
  color: var(--color-main-black);
  margin-bottom: 65px;
  font-family: var(--font-family-main); }

.portfolio__list {
  padding: 0;
  margin: 0;
  list-style: none; }
  @media (min-width: 1024px) {
    .portfolio__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around; } }

.portfolio__img {
  width: 48%;
  max-width: 400px; }

.portfolio__img-main {
  position: absolute;
  height: 92%;
  top: 20px;
  left: 0;
  right: 0;
  margin: auto;
  background-color: var(--color-bg-white); }

.portfolio__describe {
  font-family: var(--font-family-little);
  margin-top: 30px;
  margin-bottom: 50px;
  cursor: pointer; }

.portfolio__describe-link {
  color: var(--color-bg-black); }

.portfolio__list-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  position: relative; }

.contacts {
  text-align: center;
  padding-top: 50px;
  margin-top: -16px; }

.contacts__title {
  font-size: 34px;
  line-height: 42px;
  font-family: var(--font-family-main);
  margin-bottom: 27px; }

.contacts__welcome {
  font-size: 18px;
  line-height: 22px;
  width: 255px;
  height: 44px;
  margin: auto;
  margin-bottom: 39px; }

.contacts__button {
  width: 70%;
  height: 42px;
  background-color: var(--color-main-black);
  color: var(--color-bg-white);
  border-radius: 50px;
  font-size: 18px;
  line-height: 22px;
  font-family: var(--font-family-little);
  margin-bottom: 50px;
  cursor: pointer; }

.contacts__list-social {
  padding: 0;
  margin: 0 auto;
  margin-bottom: 50px;
  list-style: none;
  display: flex;
  justify-content: space-around; }
  @media (min-width: 768px) {
    .contacts__list-social {
      max-width: 70%; } }

.contacts__describe-link {
  font-size: 18px;
  line-height: 22px;
  color: var(--color-dark-grey); }

.contacts__describe-link-soc {
  margin: 0; }

.contacts__img {
  width: 40px;
  height: 40px;
  cursor: pointer; }

.wrapper {
  position: absolute;
  display: none;
  z-index: 3;
  width: 100%;
  height: 100%;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-bg-grey-popup); }

.wrapper__popup {
  position: fixed;
  margin: auto;
  width: 300px;
  height: 557px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-bg-white); }
  @media (min-width: 768px) {
    .wrapper__popup {
      width: 600px; } }
  @media (min-width: 1200px) {
    .wrapper__popup {
      width: 900px;
      height: 703px; } }

.wrapper__popup-close-button {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 25px;
  height: 25px;
  border: none;
  display: block;
  background-color: transparent; }
  .wrapper__popup-close-button::before, .wrapper__popup-close-button::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 0px;
    width: 24px;
    height: 2px;
    background-color: var(--color-main-black); }
  .wrapper__popup-close-button::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .wrapper__popup-close-button::after {
    transform: rotate(-45deg); }

.wrapper__popup-title {
  font-family: var(--font-family-main);
  font-size: 30px;
  margin-left: 15px; }
  @media (min-width: 1200px) {
    .wrapper__popup-title {
      font-size: 40px;
      margin-left: 15px;
      margin-bottom: 13px;
      margin-top: 24px; } }

.wrapper__popup-block {
  overflow: hidden;
  width: 100%;
  height: 40%;
  position: relative; }
  @media (min-width: 768px) {
    .wrapper__popup-block {
      height: 55%; } }
  @media (min-width: 1200px) {
    .wrapper__popup-block {
      height: 69%; } }

.wrapper__popup-img {
  position: absolute;
  width: 100%;
  height: auto; }

.wrapper__popup-describe {
  margin-top: 20px;
  padding-right: 10px;
  padding-left: 10px;
  font-family: var(--font-family-little);
  font-size: 14px; }
